<template>
<template v-if="modelValue.type==='grid'">
  <v-row :style="modelValue.generateCss()" >
    <template v-bind:key="index" v-for="(element,index) in modelValue.children">
      <render-items-view v-model="item.children[index]"></render-items-view>
    </template>
  </v-row>
</template>
<template v-if="modelValue.type === 'column'">
  <v-col :style="item.generateCss()" >
    <template v-bind:key="index" v-for="(element,index) in modelValue.children">
      <render-items-view v-model="item.children[index]"></render-items-view>
    </template>
  </v-col>
</template>
<template v-if="modelValue.type === 'position'">

  <div
     :style="modelValue.generateCss()"
  >
    <template v-bind:key="index" v-for="(element,index) in modelValue.children">
      <render-items-view v-model="item.children[index]"></render-items-view>
    </template>
  </div>

</template>
<template v-if="modelValue.type === 'card'">
  <v-card  :style="modelValue.generateCss()">
    <v-card-title  v-if="modelValue.title">{{modelValue.title}}</v-card-title>
    <v-card-title  v-if="!modelValue.title">Title</v-card-title>
    <v-card-subtitle  v-if="!modelValue.subtitle">Sub Title</v-card-subtitle>
    <v-card-subtitle  v-if="modelValue.subtitle">{{modelValue.subtitle}}</v-card-subtitle>
    <v-card-text >{{modelValue.text ? modelValue.text:'Lorem ipsum dolar sit amet'}}</v-card-text>
  </v-card>
</template>
  <template v-if="modelValue.type === 'nft'">
    <render-items-view  v-if="editorPage" v-model="editorPage"></render-items-view>
    <div @click="emit('itemSelect', modelValue.id)" v-else><div></div></div>
  </template>
  <template v-if="modelValue.type === 'nft_list'">
    <render-items-view  v-if="editorPage" v-model="editorPage"></render-items-view>
    <div @click="emit('itemSelect', modelValue.id)" v-else><div></div></div>
  </template>
  <template v-if="modelValue.type === 'button'">
  <v-btn></v-btn>
</template>
<template v-if="modelValue.type === 'header'">
  <h1    :style="modelValue.generateCss()">{{ modelValue.text ? modelValue.text : 'Header' }}</h1>
</template>
<template v-if="modelValue.type === 'text'">
  <div   :style="modelValue.generateCss()">{{ modelValue.text ? modelValue.text : 'lorem ipsum dolar sit amet' }}</div>
</template>
<template v-if="modelValue.type === 'image'">

  <img style="max-width: 900px" :height="item.height ? item.height : ''" :width="item.width ? item.width : ''" v-if="modelValue.image" :src="modelValue.image"  />
  <div v-else style="background: #ccc; border: 1px solid #333;text-align: center;vertical-align: center; width: 150px;height: 150px" >
    Please select a image
  </div>

</template>
<template v-if="modelValue.type === 'text_link'">
  <a >{{modelValue.text ? modelValue.text : 'Text Button / Link'}}</a>
</template>
<template v-if="modelValue.type === 'spacer'">
  <v-spacer></v-spacer>
</template>
</template>

<script setup>
import { defineProps, defineEmits, computed } from 'vue'
import { Editor } from '@/components/lib'

const props = defineProps({
  modelValue: Object,
  editor: Editor
})
const emit = defineEmits(['update:modelValue', 'drop', 'itemSelect'])
const item = computed({
  get: () => props.modelValue,
  set: (val) => {
    emit('update:modelValue', val)
  }
})
const editorPage = computed({
  get: () => props.modelValue.page,
  set: (val) => {
    emit('update:modelValue', val)
  }
})
console.log(props)
</script>

<style scoped>
.droppable {
  min-height: 24px !important;
  border: 1px dashed #cccccc;
  margin:3px 3px 3px 3px;
}
.selectedElement {
  border-color: chartreuse!important;
}

.droppable:hover {
  border: 1px dashed red !important;
}
</style>
