
import { defineComponent } from 'vue'
import { Editor } from '@/components/lib'
import PropertiesView from '@/components/Properties.vue'
import VinComponent from '@/components/AllComponents.vue'

// Components

export default defineComponent({
  name: 'HomeView',

  components: {
    VinComponent,
    PropertiesView
  },
  data: () => ({
    editor: new Editor(),
    selectedPage: 0,
    pageInput: false,
    showMenu: false,
    menuProps: false,
    showCase: true,
    pageName: '',
    dragStartItem: new EventTarget(),
    lastDrop: new Date()
  }),
  watch: {
    selectedPage (newVal, oldVal) {
      this.editor.selectedPage = this.editor.pages[newVal]
    }
  },
  methods: {
    changeMenuPos (e:any) {
      if (e.clientY) {
        const element = document.querySelector('#ItemMenu')!
        element.setAttribute('style', `top:${e.clientY}px;left:${e.clientX}px`)
        e.stopPropagation() // stops the browser from redirecting.
        // e.preventDefault()
      } else {
        console.log(e)
      }

      return false
    },
    changePropsPos (e:any) {
      if (e.clientY) {
        const element = document.querySelector('#propertiesMenu')!
        element.setAttribute('style', `top:${e.clientY}px;left:${e.clientX - 200}px`)
        e.preventDefault()
      } else {
        console.log(e)
        e.stopPropagation() // stops the browser from redirecting.
      }

      return false
    },
    select (element:string) {
      console.log(element)
      this.editor.setSelected(element)
      this.menuProps = true
    },
    handleDragStart  (e:Event) {
      console.log(e)
      console.log('drag start')
      if (e.target) this.dragStartItem = e.target as HTMLElement
    },
    handleDragOver (e:Event) {
      e.preventDefault()
      return false
    },
    handleDrop  (e:Event) {
      e.stopPropagation() // stops the browser from redirecting.
      const timed = new Date().getTime() - this.lastDrop.getTime()
      const lastTime = timed / 1000
      console.log('last time', lastTime)
      if (lastTime < 2.0) {
        return false
      }
      this.lastDrop = new Date()
      const currentTarget = e.target as HTMLElement
      console.log(e.target)
      console.log(currentTarget)
      const currentAttrs:NamedNodeMap = currentTarget.attributes
      const dataAttr = currentAttrs.getNamedItem('data')
      let id = ''
      if (dataAttr !== null) {
        id = dataAttr.value
      }
      console.log(this.dragStartItem)
      const item:HTMLElement = this.dragStartItem as HTMLElement
      const attrs:NamedNodeMap = item.attributes as NamedNodeMap
      console.log(attrs)
      if (attrs !== undefined) {
        if (attrs.getNamedItem('data') !== null) {
          const value = attrs.getNamedItem('data')
          if (value !== null) {
            this.editor.addItem(id, value.value)
          }
        }
      }
      const target:HTMLElement = e.target as HTMLElement
      console.log(target)
      target.style.border = '1px dashed #ccc'
      console.log('-------------------')
      return false
    },
    handleDragEnd  (e:Event) {
      console.log(e)
      console.log('drag end')
      const target:HTMLElement = e.target as HTMLElement
      console.log(target)
      target.style.border = '1px dashed #ccc'
    },
    handleDragEnter  (e:Event) {
      console.log('drag enter')
      const target:HTMLElement = e.target as HTMLElement
      console.log(target)
      target.parentElement!.style.border = '1px dashed #ccc'
      target.style.border = '1px dashed red'
      console.log(target.style)
    },
    handleDragLeave  (e:Event) {
      console.log(e)
      console.log('drag handleDragLeave')
      const target:HTMLElement = e.target as HTMLElement
      console.log(target)
      target.style.border = '1px dashed #ccc'
    },
    createPage () {
      this.editor.addPage(this.editor.createPage(this.pageName, true, false))
      this.pageName = ''
      this.pageInput = false
    },
    initDropDrag () {
      console.log('iinit')
      const items = document.querySelectorAll('.draggable')
      const drops = document.querySelectorAll('.droppable')
      drops.forEach((item) => {
        item.addEventListener('dragenter', this.handleDragEnter)
        item.addEventListener('dragleave', this.handleDragLeave)
        item.addEventListener('drop', this.handleDrop)
        item.addEventListener('ondrop', this.handleDrop)
      })
      items.forEach((item) => {
        item.addEventListener('dragstart', this.handleDragStart)
        item.addEventListener('dragover', this.handleDragOver)
        item.addEventListener('dragend', this.handleDragEnd)
        // item.addEventListener('drop', this.handleDrop)
      })
    }
  },
  deleteItem () {
    console.log('delete item')
  },
  created () {
    this.editor.downloadFonts()
    this.editor.addPage(this.editor.createPage('Landing Page', true, false))
    this.editor.addPage(this.editor.createPage('Nft Listing', true, false))
    this.editor.addPage(this.editor.createPage('Nft Detail', true, false))
    this.editor.addPage(this.editor.createPage('Nft View', false, true))
    this.editor.selected = this.editor.pages[0].children[0]
    this.editor.selectedPage = this.editor.pages[1]
    document.addEventListener('DOMContentLoaded', (event) => {
      setTimeout(this.initDropDrag, 1000)
    })
  }
})
