               <v-divider></v-divider>
               <v-card-actions>
                 <v-btn
                   color="blue-darken-1"
                   text
                   @click="dialog = false"
                 >
                   Close
                 </v-btn>
                 <v-btn
                   color="blue-darken-1"
                   text
                   @click="dialog = false"
                 >
                   Save
                 </v-btn>
               </v-card-actions>
             </v-card>
           </v-dialog>
         </v-col>
        </v-row>
          <v-row>
          <v-col>
            Weight
          </v-col>
          <v-col>
            <v-select  item-text="name" outlined v-model="item.css.font.weight" :items="fontWeight"></v-select>
          </v-col>
          </v-row>
          <v-row>
          <v-col>
            Size
          </v-col>
          <v-col>
            <v-text-field v-model="item.css.font.size"></v-text-field>
          </v-col>
          </v-row>
          <v-row>
            <v-col>Color</v-col>
            <v-col>
              <v-dialog>
                <template v-slot:activator="{ props }">
                  <div
                    variant="text"
                    size="small"
                    dark
                    v-bind="props"
                  >
                    <div :style="'width: 20px;height: 20px;border:1px solid #000;margin-left:20px;background: ' + item.css.color"></div>
                  </div>
                </template>
                <v-color-picker
                  dot-size="17"
                  mode="hexa"
                  v-model="item.css.color"
                  show-swatches
                  swatches-max-height="100"
                ></v-color-picker>
              </v-dialog>
            </v-col>
          </v-row>

      </v-expansion-panel-text>
    </v-expansion-panel>
    <v-expansion-panel>
      <v-expansion-panel-title>Text Properties</v-expansion-panel-title>
      <v-expansion-panel-text>Font</v-expansion-panel-text>
    </v-expansion-panel>
  </v-expansion-panels>
</template>

<script>
import { defineComponent, computed } from 'vue'
import {
  BackgroundPosition,
  BackgroundSize,
  BackgroundRepeat,
  BorderType,
  Editor,
  FontWeight,
  Page
} from '@/components/lib'
export default defineComponent({
  name: 'PropertiesView',
  props: {
    modelValue: Object,
    editor: Editor,
    selectedPage: Page
  },
  setup (props, { emit }) {
    console.log(props)
    const headers = computed({
      get: () => {
        const headersr = props.editor.findItemByType('header', props.editor.selectedPage)
        console.log(headersr)
        const pheaders = []
        headersr.forEach(element => {
          pheaders.push({
            title: element.text,
            value: element.id
          })
        })
        return pheaders
      }
    })
    const item = computed({
      get: () => props.modelValue,
      set: (val) => {
        emit('update:modelValue', val)
      }
    })
    return { item, headers }
  },
  methods: {
    deleteItem () {
      this.editor.deleteItem(this.item.id)
    },
    openFile (e) {
      console.log(e.target.files)
      const fr = new FileReader()
      fr.onload = () => {
        this.item.css.background.backgroundImage = fr.result.toString()
        console.log(this.item.css.background.backgroundImage)
      }
      fr.readAsDataURL(e.target.files[0])
    },
    isDetail (item) {
      for (const i in this.editor.pages) {
        if (this.editor.pages[i].id === item) {
          if (this.editor.pages[i].name === 'Nft Detail') {
            return true
          }
        }
      }
      return false
    },
    onImageFile (e) {
      console.log(e.target.files)
      const fr = new FileReader()
      fr.onload = () => {
        this.item.image = fr.result.toString()
      }
      fr.readAsDataURL(e.target.files[0])
    }
  },
  created () {
    for (const i in BackgroundPosition) {
      this.positions.push({
        title: i,
        value: BackgroundPosition[i]
      })
    }
    for (const i in BackgroundSize) {
      this.size.push({
        title: i,
        value: BackgroundSize[i]
      })
    }
    for (const i in BackgroundRepeat) {
      this.repeat.push({
        title: i,
        value: BackgroundRepeat[i]
      })
    }
    for (const i in BorderType) {
      this.borderType.push({
        title: i,
        value: BorderType[i]
      })
    }
    for (const i in FontWeight) {
      this.fontWeight.push({
        title: i.replaceAll('f', ''),
        value: FontWeight[i]
      })
    }
    for (const i in this.editor.pages) {
      if (!this.editor.pages[i].isNft) {
        this.pages.push({
          title: this.editor.pages[i].name,
          value: this.editor.pages[i].id,
          isDetail: this.editor.pages[i].name === 'Nft Detail'
        })
      }
    }
    for (const i in this.editor.pages) {
      if (this.editor.pages[i].isNft) {
        this.nftPages.push({
          title: this.editor.pages[i].name,
          value: this.editor.pages[i].children[0]
        })
      }
    }
  },
  data: () => ({
    color: '',
    positions: [],
    pages: [],
    nftPages: [],
    size: [],
    fontWeight: [],
    dialog: false,
    addTimeline: false,
    borderType: [],
    timeLineItem: {},
    panel: null,
    repeat: []
  })
})
</script>

