import { v4 as uuid } from 'uuid'
import axios from 'axios'

function addCssText (text: string, key: string, value: string) {
  if (value !== '') {
    if (text !== '') {
      text += ';'
    }
    text += `${key}:${value}`
  }
  return text
}
function addCssTextPrefix (text: string, key: string, value: string, prefix:string) {
  if (value !== '') {
    if (text !== '') {
      text += ';'
    }
    text += `${key}:${value}${prefix}`
  }
  return text
}

enum Position {
  Top = 'top',
  Bottom = 'bottom',
  Left = 'left',
  Right = 'right'
}

export enum Align {
  Center = 'center',
  Left = 'left',
  Right = 'right',
  Justify = 'justify'
}

export enum FontWeight {
  f100 = '100',
  f200 = '200',
  f300 = '300',
  f400 = '400',
  f500 = '500',
  fNormal = 'normal',
  fBold = 'bold',
}

export enum BackgroundRepeat {
  RepeatX = 'repeat-x',
  RepeatY = 'repeat-Y',
  NoRepeat = 'no-repeat',
  Round = 'round',
  Space = 'space'
}

export enum BackgroundPosition {
  Top = 'top',
  Bottom = 'bottom',
  Left = 'left',
  Right = 'right',
  Center = 'center'
}

export enum BackgroundSize {
  Auto = 'auto',
  Contain = 'contain',
  Cover = 'cover'
}

export enum BorderType {
  None = 'none',
  Dashed = 'dashed',
  Solid = 'solid'
}

export class Border {
  location = ''
  color = ''
  size = ''
  type: BorderType = BorderType.None
  leftRadius = ''
  rightRadius = ''

  constructor (loc: string) {
    this.location = loc
  }

  generateCss () {
    let css = ''
    const size = this.size ? (this.size + 'px') : ''
    css = addCssText(css, `border-${this.location}-width`, size)
    css = addCssText(css, `border-${this.location}-color`, this.color)
    css = addCssText(css, `border-${this.location}-type`, this.type)
    if (this.location === 'top' || this.location === 'bottom') {
      css = addCssText(css, `border-${this.location}-left-radius`, this.leftRadius)
      css = addCssText(css, `border-${this.location}-right-radius`, this.rightRadius)
    }
    return css
  }
}

class Font {
  family = ''
  size = ''
  weight: FontWeight = FontWeight.fNormal

  generateCss () {
    let css = ''
    const size = this.size ? (this.size + 'px') : ''
    css = addCssText(css, 'font-size', size)
    css = addCssText(css, 'font-family', this.family)
    css = addCssText(css, 'font-weight', this.weight)
    return css
  }
}

class TextShadow {
  horizontalShadow = ''
  verticalShadow = ''
  blurRadius = ''
  color = ''

  generateCss () {
    if (this.horizontalShadow === '') return ''
    const css = `text-shadow: ${this.horizontalShadow} ${this.verticalShadow} ${this.blurRadius} ${this.color}`
    return css
  }
}

class TextElement {
  align: Align = Align.Left
  shadow: TextShadow = new TextShadow()
  indent = ''

  generateCss () {
    let css = ''
    css = addCssText(css, 'text-indent', this.indent)
    css = addCssText(css, 'text-align', this.align)
    css = addCssText(css, 'text-shadow', this.shadow.generateCss())
    return css
  }
}

export class ItemPosition {
  public top = ''
  public left = ''
  public right = ''
  public bottom = ''

  generateCss () {
    let css = ''
    if (this.top !== '' || this.left !== '' || this.right !== '' || this.bottom !== '') css = addCssText(css, 'position', 'absolute')
    css = addCssTextPrefix(css, 'top', this.top, 'px')
    css = addCssTextPrefix(css, 'left', this.left, 'px')
    css = addCssTextPrefix(css, 'right', this.right, 'px')
    css = addCssTextPrefix(css, 'bottom', this.bottom, 'px')
    return css
  }
}

export class Background {
  public backgroundColor = ''
  public backgroundImage = ''
  public backgroundRepeat: BackgroundRepeat = BackgroundRepeat.NoRepeat
  public backgroundSize: BackgroundSize = BackgroundSize.Auto
  public backgroundPosition: BackgroundPosition = BackgroundPosition.Center

  generateCss () {
    let css = ''
    console.log(this.backgroundImage)
    const image = this.backgroundImage ? 'url(' + this.backgroundImage + ')' : ''
    css = addCssText(css, 'background-color', this.backgroundColor)
    css = addCssText(css, 'background-image', image)
    css = addCssText(css, 'background-repeat', this.backgroundRepeat)
    css = addCssText(css, 'background-size', this.backgroundSize)
    css = addCssText(css, 'background-position', this.backgroundPosition)
    console.log(css)
    return css
  }
}
class Margin {
  public top = ''
  public bottom = ''
  public left = ''
  public right = ''
  generateCss () {
    let css = ''
    css = addCssTextPrefix(css, 'margin-top', this.top, 'px')
    css = addCssTextPrefix(css, 'margin-bottom', this.bottom, 'px')
    css = addCssTextPrefix(css, 'margin-left', this.left, 'px')
    css = addCssTextPrefix(css, 'margin-right', this.right, 'px')
    return css
  }
}

class Padding {
  public top = ''
  public bottom = ''
  public left = ''
  public right = ''
  generateCss () {
    let css = ''
    css = addCssTextPrefix(css, 'padding-top', this.top, 'px')
    css = addCssTextPrefix(css, 'padding-bottom', this.bottom, 'px')
    css = addCssTextPrefix(css, 'padding-left', this.left, 'px')
    css = addCssTextPrefix(css, 'padding-right', this.right, 'px')
    return css
  }
}

class Css {
  public color = ''
  public font: Font = new Font()
  public text: TextElement = new TextElement()
  public background: Background = new Background()
  public textAlign: Align = Align.Left
  public BorderTop: Border = new Border('top')
  public BorderLeft: Border = new Border('left')
  public BorderRight: Border = new Border('right')
  public BorderBottom: Border = new Border('bottom')
  public margin: Margin = new Margin()
  public padding: Padding = new Padding()
  public position: ItemPosition = new ItemPosition()
  public CustomCss = ''
  public width = ''
  public height = ''

  setCss (val: string) {
    this.CustomCss = val
  }

  generateCss () {
    let css = ''
    css = addCssText(css, 'color', this.color)
    css = addCssText(css, 'text-align', this.textAlign)
    css = addCssTextPrefix(css, 'width', this.width, 'px')
    css = addCssTextPrefix(css, 'height', this.height, 'px')
    css += ';'
    css += this.background.generateCss()
    css += ';'
    css += this.margin.generateCss()
    css += ';'
    css += this.padding.generateCss()
    css += ';'
    css += this.position.generateCss()
    css += ';'
    css += this.font.generateCss()
    css += ';'
    css += this.BorderTop.generateCss()
    css += ';'
    css += this.BorderBottom.generateCss()
    css += ';'
    css += this.BorderLeft.generateCss()
    css += ';'
    css += this.BorderRight.generateCss()
    css += ';'
    css += this.text.generateCss()
    return css
  }
}
class Elements {
  public name = ''
  public id = ''
  public key = ''
  public contentType = ''
  public type = ''
  public dom:HTMLElement | null = null
  public selected = false
  public parent: any
  public children: Array<Elements> = new Array<Elements>()
  public css: Css = new Css()

  constructor (name: string, id: string, key: string) {
    this.name = name
    this.id = id
    this.key = key
  }

  getDom () {
    if (document.querySelector("*[data='" + this.id + "']") && this.dom == null) {
      /* eslint-disable */
      this.dom = document.querySelector("*[data='" + this.id + "']")!
    }
    return this.dom
  }

  setParent (element: Elements) {
    this.parent = element
  }

  generateCss () {
    return this.css.generateCss()
  }

  addChildren (element: Elements) {
    this.children.push(element)
  }
}

