export class Page {
  children: Array<Elements> = new Array<Elements>()
  id = ''
  name = ''
  isProtected = false
  isNft = false

  addChildren (element: Elements) {
    this.children.push(element)
  }

  findElementByType (typeName: string, object:Elements) {
    const foundedItems: Array<Elements> = new Array<Elements>()
    console.log(object.type,typeName)
    if (object.type === typeName) {
      foundedItems.push(object)
    }
    console.log(object)
    object.children.forEach(element => {
      console.log(element.type, typeName)
      if (element.type === typeName) {
        foundedItems.push(element)
      }
      if (element.children.length > 0) {
        const founded = this.findElementByType(typeName, element)
        console.log('before concat', founded)
        founded.forEach(element => foundedItems.push(element))
        console.log('after concat', foundedItems)
      }
    })
    console.log('elementny', foundedItems)
    return foundedItems
  }

  findItemByType (typeName: string) {
    const foundItems : Array<Elements> = new Array<Elements>()
    this.children.forEach(elem => {
      this.findElementByType(typeName, elem).forEach(element => foundItems.push(element))
      console.log('found işte', foundItems)
    })
    return foundItems
  }

  findElement (id: string, object: Elements) {
    console.log('findElement', id, object.id)
    let found: Elements = new Elements('', '', '')
    if (object.id === id) {
      console.log('foundend return')
      found = object
    }
    if (found.id === '') {
      object.children.forEach(element => {
        console.log(element.id, id)
        if (element.id === id) {
          found = element
        }
        if (element.children.length > 0 && found.id === '') {
          found = this.findElement(id, element)
        }
      })
    }
    return found
  }

  findItem (id: string) {
    console.log('find', id)
    let found: Elements = new Elements('', '', '')
    this.children.forEach(elem => {
      if (found.id === '') {
        found = this.findElement(id, elem)
      }
    })
    return found
  }

  clearSelected () {
    this.children.forEach(elem => {
      this.clearElementsSelected(elem)
    })
  }

  clearElementsSelected (object: Elements) {
    if (object.getDom()) object.getDom()!.classList.remove("selectedElement")
    object.selected = false
    object.children.forEach(element => {
      element.selected = false
      if(element.getDom()) element.getDom()!.classList.remove("selectedElement")
      if (element.children.length > 0) {
        this.clearElementsSelected(element)
      }
    })
  }
}

export class Editor {
  public pages: Array<Page> = new Array<Page>()
  public selectedPage: Page = new Page()
  public selected: Elements = new Elements('', '', '')
  public allFonts: string[] = []
  public fonts:any[] = []
  public loaded = false

  deleteItem (itemId:string) {
    this.pages.forEach(elem => {
      elem.clearSelected()
    })
    let founded: Elements = new Elements('', '', '')
    this.pages.forEach(elem => {
      if (founded.id === '') founded = elem.findItem(itemId)
    })
    console.log('founded', founded.id)
    founded.parent.children.pop(founded)
  }
  getPage(id: string) {
    let page
    this.pages.forEach(element => {
      if (element.id == id) page = element;
    })
    return page
  }
  downloadFonts () {
    axios.get('https://www.googleapis.com/webfonts/v1/webfonts?key=AIzaSyCOaJrEogpx6D2khz5vRw1j-vQtXCvvTmo').then(response => {
      let fontList:string[] = []
      this.fonts = response.data.items
      this.fonts.forEach(item => {

        fontList.push(item.family.replace(' ', '+'))
        if (fontList.length > 30) {
          const items = fontList.join('|')
          axios.get(`https://fonts.googleapis.com/css?family=${items}?key=AIzaSyCOaJrEogpx6D2khz5vRw1j-vQtXCvvTmo&font-display=swap`).then(response => {
            document.head.appendChild(document.createElement('style')).innerHTML = response.data
          })
          fontList = []
        }
      })
      const items = fontList.join('|')
      if (items != "" ) {
        axios.get(`https://fonts.googleapis.com/css?family=${items}?key=AIzaSyCOaJrEogpx6D2khz5vRw1j-vQtXCvvTmo&font-display=swap`).then(response => {
          document.head.appendChild(document.createElement('style')).innerHTML = response.data
          this.loaded = true
        })
      } else {
        this.loaded = true
      }

    })
  }

  createPage (name: string, isProtected: boolean, isNft:boolean) {
    const page = new Page()
    page.id = uuid()
    page.name = name
    page.isProtected = isProtected
    page.isNft = isNft
    const id = uuid()
    const element = new Grid(id, id, id)
    element.type = 'grid'
    page.addChildren(element)
    return page
  }

  get editorPages () {
    return this.pages
  }

  addPage (page: Page) {
    if (this.pages.length === 0) {
      this.selectedPage = page
    }
    this.pages.push(page)
  }

  findParentType (id: string, type: string) {
    let founded: Elements = new Elements('', '', '')
    this.pages.forEach(elem => {
      if (founded.id === '') founded = elem.findItem(id)
    })
    if (founded.type !== type || founded.id === '') {
      alert('cant found f:' + founded.type + ', r:' + type)
      founded = this.findParentType(founded.parent.id, type)
      return founded
    }
    return founded
  }

  addChildItem (id: string, element: Elements) {
    let founded: Elements = new Elements('', '', '')
    this.pages.forEach(elem => {
      if (founded.id === '') founded = elem.findItem(id)
    })
    if (founded.id !== '') {
      if (element.type !== 'column') {
        if (founded.type !== 'column') {
          if (!founded.parent) {
            if (founded.type === 'grid' || founded.type === 'card') {
              founded = this.addItem(founded.id, 'column')
            }
          } else {
            const item = this.findParentType(founded.parent.id, 'column')
            if (item.id === '') {
              if (founded.type === 'grid') {
                founded = this.addItem(founded.id, 'column')
              }
            } else {
              founded = item
            }
          }
        }
      }
      if (founded.type === 'column' && element.type === 'column') {
        founded = founded.parent
      }
    }
    element.parent = founded
    founded.addChildren(element)
  }

  addItem (id: string, elementType: string) {
    let element = new Elements(elementType.toLocaleUpperCase(), uuid(), uuid())
    switch (elementType) {
      case 'image':
        element = new Image(elementType.toLocaleUpperCase(), uuid(), uuid())
        break
      case 'header':
        element = new Header(elementType.toLocaleUpperCase(), uuid(), uuid())
        break
      case 'text':
        element = new Text(elementType.toLocaleUpperCase(), uuid(), uuid())
        break
      case 'text_link':
        element = new TextButton(elementType.toLocaleUpperCase(), uuid(), uuid())
        break
      case 'col':
        element = new Cols(elementType.toLocaleUpperCase(), uuid(), uuid())
        break
      case 'column':
        element = new Cols(elementType.toLocaleUpperCase(), uuid(), uuid())
        break
      case 'card':
        element = new Card(elementType.toLocaleUpperCase(), uuid(), uuid())
        break
      case 'grid':
        element = new Grid(elementType.toLocaleUpperCase(), uuid(), uuid())
        break
      case 'time_line':
        element = new Timeline(elementType.toLocaleUpperCase(), uuid(), uuid())
        break
    }

    element.type = elementType
    this.addChildItem(id, element)
    if (elementType === 'card') {
      const subElement = new Grid('grid'.toLocaleUpperCase(), uuid(), uuid())
      subElement.type = 'grid'
      element.children.push(subElement)
    }
    return element
  }

  findItemByType (typeName: string, page:Page) {
    console.log("-----------------------------------")
    const foundedItems: Array<Elements> = new Array<Elements>()
    if (page !== null) {
      const items = page.findItemByType(typeName)
      items.forEach(element => foundedItems.push(element))
      return foundedItems
    } else {
      this.pages.forEach(elem => {
        elem.findItemByType(typeName).forEach(element => foundedItems.push(element))
      })
    }
    return foundedItems
  }

  setSelected (id: string) {
    this.pages.forEach(elem => {
      elem.clearSelected()
    })
    let founded: Elements = new Elements('', '', '')
    this.pages.forEach(elem => {
      if (founded.id === '') founded = elem.findItem(id)
    })
    console.log('founded', founded.id)
    if (founded.id !== '') {
      founded.selected = true
      founded.getDom()!.classList.add("selectedElement")
      console.log('founded', founded.id)
    }
    this.selected = founded
  }
}
